.exam-list {
  width: 100%;
  height: auto;

  * {
    font-style: normal;
  }

  .header {
    background-color: transparent;
    box-shadow: none;
  }

  .tools-nav {
    margin: 12px 0 4px;
    height: 33px;
    display: flex;
    margin-right: 8px;
    font-size: 12px;

    .list-type {
      background-color: white;
      width: 75px;
      height: 30px;
      display: flex;
      align-items: center;
      justify-content: center;
      margin-right: 8px;

      .iconfont {
        cursor: pointer;
      }

      .line {
        margin: 0 8px;
        width: 2px;
        height: 14px;
        background-color: #ccc;

      }

      .icon-24gl-appsBig {
        font-size: 16px;

        &.active {
          color: #2672ff;
        }
      }

      .icon-ziyuan {
        font-size: 14px;

        &.active {
          color: #2672ff;
        }
      }
    }
  }

  &.list-table {
    .exam-content {
      .list {
        display: none;
      }

      li {
        &.add-pro {
          display: none;
        }
      }
    }
  }

  .exam-content {
    width: 100%;
    height: auto;
    padding-top: 18px;

    .grid-list {
      overflow: hidden;

      li {
        width: 279px;
        height: 176px;
        float: left;
        background-color: white;
        box-shadow: -2px 4px 5px 0 rgba(0, 0, 0, .06);
        margin-right: 18px;
        margin-bottom: 18px;

        &:hover {
          cursor: pointer;
          box-shadow: -2px 8px 10px 0 rgba(0, 0, 0, .08);

          &.project {
            .operate-bar {
              cursor: auto;
              display: block;

              &>p,
              &>div.more {
                cursor: pointer;
              }
            }

            &.not-allowed {
              cursor: default;
            }
          }
        }

        &:nth-child(4n) {
          margin-right: 0;
        }

        &.add-pro {
          display: flex;
          justify-content: center;
          align-items: center;
          font-size: 15px;
          color: #2672ff;

          .iconfont {
            font-size: 16px;
            padding-right: 10px;
          }
        }

        &.project {
          padding: 18px;
          position: relative;
          font-size: 12px;

          &>* {
            display: inline-block;
          }

          p {
            width: 100%;
            margin: 0;
          }

          .title,
          .status {
            vertical-align: top;
          }

          .title {
            font-size: 14px;
            margin-bottom: 8px;
          }

          .status {
            color: #ffbe3a;
          }

          .judge {
            width: 24px;
            height: 24px;
            position: absolute;
            left: 20px;
            bottom: 50px;
            cursor: pointer;

            &:hover {
              color: #2672ff;
            }
          }

          .info {
            width: 243px;
            vertical-align: bottom;
            position: absolute;
            left: 18px;
            bottom: 18px;
            color: #aaa;

            .time {
              float: right;
            }
          }

          .operate-bar {
            position: absolute;
            display: none;
            left: 0;
            bottom: 0;
            z-index: 5;
            width: 279px;
            height: 46px;
            background: #f7f8fa;
            border-radius: 0 0 2px 2px;
            padding: 0 18px;
            color: #707070;

            &>p {
              float: left;
              width: 51.3px;
              height: 100%;
              line-height: 46px;
              display: flex;
              justify-content: center;
              align-items: center;
              margin-right: 10px;

              &:hover {
                color: #2672ff;
              }

              &.not-allowed:hover {
                color: #707070;
                cursor: not-allowed;
              }

              .iconfont {
                margin-right: 5px;
              }
            }

            &>.more {
              position: absolute;
              right: 10px;
              top: 50%;
              width: 30px;
              // padding-left: 15px;
              height: 30px;
              text-align: center;
              line-height: 30px;
              transform: translateY(-50%);

              .iconfont {
                font-size: 24px;
              }

              &:hover {
                color: #2672ff;
              }
            }
          }
        }
      }
    }
  }
}

.operation-more {

  .judge,
  .stop,
  .delete {
    cursor: pointer;
    padding: 2px 5px;

    &:hover {
      background-color: #f7f8fa;
    }
  }

  .ant-popover-inner-content {
    padding-left: 8px;
    padding-right: 8px;
  }
}